  padding-bottom: 100px;
}
@media (max-width: 1366px) {
  .catalog {
    padding-top: 88px;
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  .catalog {
    padding-top: 70px;
    padding-bottom: 60px;
  }
}
.catalog__title {
  font-family: "BebasNeue";
  font-weight: 700;
  font-size: 42px;
  line-height: 38px;
  color: var(--primary-color);
  margin: 0;
  margin-bottom: 46px;
}
@media (max-width: 768px) {
  .catalog__title {
    font-size: 36px;
    margin-bottom: 23px;
  }
}

.new {
  padding-bottom: 95px;
}
@media (max-width: 1921px) {
  .new {
    padding-bottom: 65px;
  }
}
@media (max-width: 1366px) {
  .new {
    padding-bottom: 40px;
  }
}
@media (max-width: 768px) {
  .new {
    padding-bottom: 25px;
  }
}
.new__title {
  font-family: "BebasNeue";
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;
  margin: 0;
  margin-bottom: 25px;
}
@media (max-width: 1366px) {
  .new__title {
    margin-bottom: 40px;
  }
}
@media (max-width: 768px) {
  .new__title {
    font-size: 28px;
    line-height: 38px;
    margin-bottom: 55px;
  }
}
.new__sale {
  position: relative;
}
.new__sale::before {
  content: "";
  position: absolute;
  top: 0;
  right: 25px;
  width: 95px;
  height: 95px;
  background-image: url(../img/new-sale.svg);
  background-repeat: no-repeat;
}
@media (max-width: 768px) {
  .new__sale::before {
    top: 10px;
    right: 10px;
  }
}
.new__content {
  position: relative;
  padding-top: 60px;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: -60px;
  margin-left: -15px;
  margin-right: -15px;
}
@media (max-width: 1366px) {
  .new__content {
    padding-top: 77px;
    margin-top: -77px;
    padding-bottom: 35px;
  }
}
@media (max-width: 768px) {
  .new__content {
    padding-top: 43px;
  }
}
.new__btns {
  position: absolute;
  top: 5px;
  right: 12px;
}
@media (max-width: 768px) {
  .new__btns {
    top: -10px;
  }
}
.new__slider-btn {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 41px;
  height: 9px;
}
.new__slider-btn--prev {
  background-image: url(../img/arr-card.svg);
  transform: matrix(-1, 0, 0, 1, 0, 0);
}
.new__slider-btn--next {
  background-image: url(../img/arr-card.svg);
  margin-left: 10px;
}
.new__pagination {
  text-align: center;
  margin-top: 50px;
}
@media (max-width: 768px) {
  .new__pagination {
    margin-top: 20px;
  }
}
.new__pagination .swiper-pagination-bullet {
  border: 2px solid #039583;
  border-radius: 2px;
  width: 22px;
  height: 22px;
  background-color: transparent;
}
.new__pagination .swiper-pagination-bullet-active {
  border: 2px solid #039583;
  border-radius: 2px;
  position: relative;
}
.new__pagination .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 14px;
  height: 14px;
  background-color: #039583;
  transform: translate(-50%, -50%);
}
@media (max-width: 1366px) {
  .new .cards__title {
    width: 200px;
  }
}

.swiper-button-disabled {
  opacity: 0.6;
}

.post {
  padding-bottom: 90px;
}
@media (max-width: 1366px) {
  .post {
    padding-bottom: 70px;
  }
}
@media (max-width: 768px) {
  .post {
    padding-bottom: 55px;
  }
}
.post__title {
  font-family: "BebasNeue";
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;
  margin: 0;
  margin-bottom: 14px;
}
@media (max-width: 768px) {
  .post__title {
    font-size: 28px;
    line-height: 38px;
    margin-bottom: 23px;
  }
}
.post__content {
  position: relative;
  padding-top: 60px;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: -45px;
  margin-left: -15px;
  margin-right: -15px;
}
@media (max-width: 1921px) {
  .post__content {
    padding-top: 73px;
  }
}
@media (max-width: 768px) {
  .post__content {
    padding-top: 43px;
  }
}
.post__pagination {
  text-align: center;
  margin-top: 50px;
}
@media (max-width: 1921px) {
  .post__pagination {
    margin-top: 25px;
  }
}
.post__pagination .swiper-pagination-bullet {
  border: 2px solid #039583;
  border-radius: 2px;
  width: 22px;
  height: 22px;
  background-color: transparent;
}
.post__pagination .swiper-pagination-bullet-active {
  border: 2px solid #039583;
  border-radius: 2px;
  position: relative;
}
.post__pagination .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 14px;
  height: 14px;
  background-color: #039583;
  transform: translate(-50%, -50%);
}

.post-card {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);
}
.post-card__item {
  grid-column: 3 span;
}
.post-card__date {
  display: block;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: var(--date-color);
  margin-top: 10px;
  margin-bottom: 25px;
}
@media (max-width: 768px) {
  .post-card__date {
    font-size: 16px;
    line-height: 19px;
    margin-top: 17px;
    margin-bottom: 17px;
  }
}
.post-card__title {
  font-family: "BebasNeue";
  font-weight: 400;
  font-size: 28px;
  line-height: 38px;
  color: var(--primary-color);
  margin: 0;
  margin-bottom: 12px;
}
@media (max-width: 768px) {
  .post-card__title {
    font-size: 24px;
    line-height: 38px;
    margin-bottom: 5px;
  }
}
.post-card__text {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: var(--primary-color);
  margin: 0;
  margin-bottom: 30px;
}
@media (max-width: 768px) {
  .post-card__text {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 10px;
  }
}
.post-card__link {
  font-size: 20px;
  line-height: 24px;
  color: var(--bg-nav-color);
  position: relative;
  transition: opacity 0.3s ease-in-out;
}
.post-card__link:hover {
  opacity: 0.7;
}
.post-card__link::before {
  content: "";
  position: absolute;
  right: -63px;
  top: 8px;
  width: 45px;
  height: 11px;
  background-image: url(../img/arr-card.svg);
}

.footer {
  background-color: var(--bg-color);
  padding-top: 17px;
  padding-bottom: 10px;
}
@media (max-width: 1921px) {
  .footer {
    padding-top: 18px;
  }
}
.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);
  margin-bottom: 17px;
}
@media (max-width: 768px) {
  .footer__wrapper {
    gap: var(--grid-gap-tablet);
    margin-bottom: 10px;
  }
}
@media (max-width: 576px) {
  .footer__wrapper {
    margin-bottom: 40px;
  }
}
.footer__logo {
  grid-column: 3 span;
}
@media (max-width: 1921px) {
  .footer__logo {
    grid-column: 2 span;
  }
}
@media (max-width: 768px) {
  .footer__logo {
    grid-column: 4 span;
  }
}
@media (max-width: 576px) {
  .footer__logo {
    grid-column: 5 span;
    grid-row: 1 span;
  }
}
.footer__nav {
  grid-column: 2 span;
}
@media (max-width: 768px) {
  .footer__nav {
    grid-column: 4 span;
  }
}
@media (max-width: 576px) {
  .footer__nav {
    grid-column: 5 span;
    grid-row: 1 span;
  }
}
.footer__nav--block {
  grid-column: 3 span;
}
@media (max-width: 1366px) {
  .footer__nav--block {
    grid-column: 4 span;
    margin-left: auto;
  }
}
@media (max-width: 768px) {
  .footer__nav--block {
    grid-column: 6 span;
    display: flex;
    margin-left: 0;
  }
}
@media (max-width: 576px) {
  .footer__nav--block {
    flex-direction: column;
    grid-column: 8 span;
  }
}
.footer__nav-title {
  font-weight: 800;
  font-size: 16px;
  line-height: 16px;
  color: var(--light-color);
  margin: 0;
  margin-bottom: 7px;
}
@media (max-width: 576px) {
  .footer__nav-title {
    font-size: 14px;
    line-height: 14px;
  }
}
.footer__nav-link {
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 3px;
  width: 50px;
  height: 30px;
  padding: 5px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: bottom;
  transition: background-color 0.3s ease-in-out;
}
@media (max-width: 768px) {
  .footer__nav-link {
    margin-bottom: 10px;
  }
}
.footer__nav-link:hover {
  background-color: var(--light-color);
}
.footer__nav-link:not(:last-child) {
  margin-right: 5px;
}
@media (max-width: 1024px) {
  .footer__nav-link:not(:last-child) {
    margin-right: 2px;
  }
}
@media (max-width: 768px) {
  .footer__nav-link:not(:last-child) {
    margin-right: 5px;
  }
}
.footer__link {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: var(--footerLink-color);
  transition: color 0.3s ease-in-out;
}
.footer__link:hover {
  color: var(--footerLink-color-hover);
}
.footer__link:not(:last-child) {
  margin-bottom: 10px;
}
.footer__link-text {
  vertical-align: super;
}
@media (max-width: 768px) {
  .footer__nav-soc {
    transform: translateX(75px);
  }
}
@media (max-width: 576px) {
  .footer__nav-soc {
    transform: translateX(0);
  }
}
.footer__nav-img {
  margin-bottom: 25px;
}
@media (max-width: 768px) {
  .footer__nav-img {
    max-width: 170px;
  }
}
@media (max-width: 576px) {
  .footer__nav-img {
    max-width: 100%;
  }
}
.footer__nav-link-social {
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 3px;
  width: 30px;
  height: 30px;
  padding: 5px;
  display: inline-flex;
  justify-content: center;
  transition: background-color 0.3s ease-in-out;
}
.footer__nav-link-social:hover {
  background-color: var(--light-color);
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 576px) {
  .footer__bottom {
    flex-direction: column;
  }
}
.footer__artix {
  fill: #FAFEFD;
  width: 20px;
  height: 21px;
  margin-left: 13px;
}