.new {
  padding-bottom: 95px;

  @include large-desktop {
    padding-bottom: 65px;
  }

  @include big-desktop {
    padding-bottom: 40px;
  }

  @include small-tablet {
    padding-bottom: 25px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    margin: 0;
    margin-bottom: 25px;

    @include big-desktop {
      margin-bottom: 40px;
    }

    @include small-tablet {
      font-size: 28px;
      line-height: 38px;
      margin-bottom: 55px;
    }
  }

  &__sale {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 25px;
      width: 95px;
      height: 95px;
      background-image: url(../img/new-sale.svg);
      background-repeat: no-repeat;

      @include small-tablet {
        top: 10px;
        right: 10px;
      }
    }
  }

  &__content {
    position: relative;
    padding-top: 60px;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: -60px;
    margin-left: -15px;
    margin-right: -15px;

    @include big-desktop {
      padding-top: 77px;
      margin-top: -77px;
      padding-bottom: 35px;
    }

    @include small-tablet {
      padding-top: 43px;
    }
  }

  &__btns {
    position: absolute;
    top: 5px;
    right: 12px;

    @include small-tablet {
      top: -10px;
    }
  }

  &__slider-btn {
    @include bg-cover;

    width: 41px;
    height: 9px;

    &--prev {
      background-image: url(../img/arr-card.svg);
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }

    &--next {
      background-image: url(../img/arr-card.svg);
      margin-left: 10px;
    }
  }

  &__pagination {
    text-align: center;
    margin-top: 50px;

    @include small-tablet {
      margin-top: 20px;
    }

    & .swiper-pagination-bullet {
      border: 2px solid #039583;
      border-radius: 2px;
      width: 22px;
      height: 22px;
      background-color: transparent;
    }

    & .swiper-pagination-bullet-active {
      border: 2px solid #039583;
      border-radius: 2px;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 14px;
        height: 14px;
        background-color: #039583;
        transform: translate(-50%, -50%);
      }
    }
  }

  & .cards__title {
    @include big-desktop {
      width: 200px;
    }
  }
}

.swiper-button-disabled {
  opacity: 0.6;
}
