.footer {
  background-color: var(--bg-color);
  padding-top: 17px;
  padding-bottom: 10px;

  @include large-desktop {
    padding-top: 18px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap);
    margin-bottom: 17px;

    @include small-tablet {
      gap: var(--grid-gap-tablet);
      margin-bottom: 10px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__logo {
    grid-column: 3 span;

    @include large-desktop {
      grid-column: 2 span;
    }

    @include small-tablet {
      grid-column: 4 span;
    }

    @include mobile {
      grid-column: 5 span;
      grid-row: 1 span;
    }
  }

  &__nav {
    grid-column: 2 span;

    @include small-tablet {
      grid-column: 4 span;
    }

    @include mobile {
      grid-column: 5 span;
      grid-row: 1 span;
    }

    &--block {
      grid-column: 3 span;

      @include big-desktop {
        grid-column: 4 span;
        margin-left: auto;
      }

      @include small-tablet {
        grid-column: 6 span;
        display: flex;
        margin-left: 0;
      }

      @include mobile {
        flex-direction: column;
        grid-column: 8 span;
      }
    }
  }

  &__nav-title {
    font-weight: 800;
    font-size: 16px;
    line-height: 16px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 7px;

    @include mobile {
      font-size: 14px;
      line-height: 14px;
    }
  }

  &__nav-link {
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    width: 50px;
    height: 30px;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: bottom;
    transition: background-color 0.3s ease-in-out;

    @include small-tablet {
      margin-bottom: 10px;
    }


    &:hover {
      background-color: var(--light-color);
    }

    &:not(:last-child) {
      margin-right: 5px;

      @include tablet {
        margin-right: 2px;
      }

      @include small-tablet {
        margin-right: 5px;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: var(--footerLink-color);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--footerLink-color-hover);
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &-text {
      vertical-align: super;
    }
  }

  &__nav-soc {
    @include small-tablet {
      transform: translateX(75px);
    }

    @include mobile {
      transform: translateX(0);
    }
  }

  &__nav-img {
    margin-bottom: 25px;

    @include small-tablet {
      max-width: 170px;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__nav-link-social {
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    width: 30px;
    height: 30px;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    transition: background-color 0.3s ease-in-out;

    &:hover {
      background-color: var(--light-color);
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mobile {
      flex-direction: column;
    }
  }

  &__artix {
    fill: #FAFEFD;
    width: 20px;
    height: 21px;
    margin-left: 13px;
  }
}
