/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin large-desktop {
  @media (max-width: (1921px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1366px)) {
    @content;
  }
}

@mixin medium-desktop {
  @media (max-width: (1240px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin large-mobile {
  @media (max-width: (690px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
