.burger {
  @include burger;
  display: none;

  @include tablet {
    display: block;
    margin-left: 20px;
    margin-top: -25px;
  }
}

.header {
  padding-top: 10px;

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__logo {

    @include mobile {
      width: 75px;
    }
  }

  &__bottom {
    background-color: var(--bg-nav-color);
    padding-top: 15px;
    padding-bottom: 15px;
    position: absolute;
    top: 72px;
    left: 0;
    width: 100%;

    @include mobile {
      top: 55px;
    }

    &-search {
      @include medium-desktop {
        margin-left: auto;
      }
    }
  }
}

.header-contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: translate(11.5%, -12px);

  @include large-desktop {
    transform: translate(0%, -12px);
  }

  @include big-desktop {
    transform: translate(10px, -12px);
  }

  @include small-tablet {
    transform: translate(26px, -12px);
  }

  &__icon {
    width: 30px;
    height: 30px;
    fill: var(--decor-color);
    margin-right: 15px;

    @include mobile {
      margin-right: 52px;
    }
  }

  &__address {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 19px;
    color: var(--primary-color);

    @include medium-desktop {
      font-size: 12px;
    }

    @include mobile {
      display: none;
    }
  }

  &__mail {
    display: flex;
    align-items: center;
    margin-left: 196px;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--primary-color);
    transition: color 0.3s ease-in-out;

    @include large-desktop {
      margin-left: 60px;
    }

    @include big-desktop {
      margin-left: 30px;
    }

    @include medium-desktop {
      font-size: 12px;
    }

    @include small-tablet {
      margin-left: 15px;
    }

    @include mobile {
      display: none;
    }

    &:hover {
      color: var(--primary-color-hover);
    }
  }

  &__tel {
    display: flex;
    align-items: center;
    margin-left: 200px;
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    font-family: "BebasNeue", sans-serif;
    color: var(--primary-color);
    transition: color 0.3s ease-in-out;

    @include large-desktop {
      margin-left: 60px;
    }

    @include big-desktop {
      margin-left: 30px;
    }

    @include small-tablet {
      margin-left: 15px;
    }

    &:hover {
      color: var(--primary-color-hover);
    }

    & .header-contacts__text {
      display: block;

      @include large-mobile {
        display: none;
      }
    }
  }

  &__text {
    @include tablet {
      display: none;
    }
  }
}

.lk {
  display: flex;
  align-items: center;
  transform: translateY(-10px);

  &__icon {
    &-login {
      width: 39px;
      height: 39px;
      fill: var(--lk-color);
      transition: fill 0.3s ease-in-out;

      @include mobile {
        width: 30px;
        height: 30px;
      }

      &:hover {
        fill: var(--lk-color-hover);
      }
    }

    &-cart {
      width: 49px;
      height: 47px;
      fill: var(--lk-color);
      transition: fill 0.3s ease-in-out;

      @include mobile {
        width: 39px;
        height: 38px;
      }

      &:hover {
        fill: var(--lk-color-hover);
      }
    }
  }

  &__login {
    padding: 16px;
    position: relative;
    border-left: 1px solid rgba(15, 193, 175, 0.2);
    border-right: 1px solid rgba(15, 193, 175, 0.2);

    @include big-desktop {
      padding: 16px;
    }

    @include mobile {
      padding: 14px;
    }
  }

  &__cart {
    padding: 14px 0 14px 14px;

    &-count {
      font-weight: 800;
      font-size: 12px;
      line-height: 1.4;
      color: var(--light-color);
      background-color: #FF9903;
      border: 1px solid #FFFFFF;
      border-radius: 100%;
      display: block;
      position: absolute;
      top: 14px;
      right: 14px;
      text-align: center;
      padding: 0 4px;
      min-width: 17px;
      height: 17px;

      @include mobile {
        font-weight: 800;
        font-size: 8px;
        line-height: 1.65;
        min-width: 14px;
        height: 14px;
        top: 10px;
        right: 0;
      }
    }

    @include big-desktop {
      padding: 14px;
    }

    @include mobile {
      padding: 6px 0 10px 8px;
    }
  }
}

.nav {

  @include tablet {
    position: fixed;
    right: 0;
    top: 0;
    overflow-y: auto;
    padding: 75px 45px 45px;
    height: 100vh;
    background-color: var(--bg-nav-color);
    box-shadow: 2px 0px 8px #494949;
    transform: translateX(100%);
    transition: transform .3s ease-in-out;
  }

  @include mobile {
    padding: 75px 25px 28px;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list {
    display: inline-flex;
    justify-content: flex-start;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 61px;

      @include tablet {
        margin-right: 15px;
        margin-bottom: 10px;
      }
    }

    &:first-child {
      border-right: 1px solid rgba(15, 193, 175, 0.2);
      padding-top: 22px;
      padding-bottom: 22px;
      padding-right: 53px;
      margin-top: -22px;
      margin-bottom: -22px;
      margin-right: 34px;
      position: relative;

      @include tablet {
        border-right: none;
        margin-bottom: 0px;
        padding-bottom: 10px;
      }

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: 30px;
        width: 8px;
        height: 10px;
        background-image: url(../img/arr.svg);
        transform: translateY(-50%);

        @include tablet {
          display: none;
        }
      }
    }
  }

  &__link {
    font-weight: 800;
    font-size: 16px;
    line-height: 16px;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    @include medium-desktop {
      font-size: 14px;
    }

    &:hover {
      color: var(--light-color-hover);
    }
  }
}

.header__search {
  width: 27px;
  height: 27px;
  fill: #0FC1AF;

  @include medium-desktop {
    margin-right: 20px;
  }

  @include tablet {
    margin-right: 0px;
  }
}

.menu--active {
  transform: translateX(0);
}

.o1 {
  background-image: url(../img/sprite.svg#tel);
  background-repeat: no-repeat;
  background-size: 20px;
  padding-left: 20px;
  background-position: 0 1px;
}
