.catalog {
  padding-top: 100px;
  padding-bottom: 100px;

  @include big-desktop {
    padding-top: 88px;
    padding-bottom: 80px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 42px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 46px;

    @include small-tablet {
      font-size: 36px;
      margin-bottom: 23px;
    }
  }
}
